import React from 'react';

export default function LoveLetterPage() {
  return (
    <div
      id="love-letter"
      className="min-vh-100 d-flex justify-content-center align-items-center text-center px-3"
      style={{
        background: 'linear-gradient(to right, #d2b48c, #e6ccb2)',
        fontFamily: "'Poppins', sans-serif",
        color: '#444',
      }}
    >
      <div
        style={{
          maxWidth: '800px',
          backgroundColor: 'rgba(255, 255, 255, 0.85)',
          borderRadius: '20px',
          padding: '40px',
          boxShadow: '0 10px 20px rgba(0,0,0,0.1)',
        }}
      >
        <h2
          style={{
            fontFamily: "'Dancing Script', cursive",
            fontSize: '2.5rem',
            color: '#8b4513',
            marginBottom: '20px',
          }}
        >
          A Letter Just for You 
        </h2>

        <p style={{ fontSize: '1.05rem', lineHeight: '1.8' }}>
          My dearest Akansha,<br /><br />
          I dont know where to begin, but I know where my heart always ends up  with you.   
          From the very first time I liked your tweet in May 2024, to the nights we stayed up talking,  
          something in you kept pulling me closer. Maybe it was your laugh. Maybe your eyes. Maybe your chaos. Or maybe just... you.  
          <br /><br />

          You say youre moody  I say youre real.  
          You say you overthink  I say you feel deeply.  
          You say you're too much  but baby, you're just enough for me.   
          <br /><br />

          I know the doctor journey isnt easy. The late nights, the pressure, the constant questioning of Am I enough?  but I promise you, you are.  
          One day, some scared patient is going to hold your hand and feel safe. Just like I do.  
          <br /><br />

          You carry the heart of a child  the one who loved Rapunzel, giggled with Olaf, and believed in magic. That magic never left you.  
          I see it every time you doodle, rant, dance, or just randomly say heyyy.  
          Thats the girl I fell for  and the woman Im still falling for, every single day.   
          <br /><br />

          Through all the kalesh, mood swings, random vanishing, and even that scary rage mode of yours   
          I still look at you and think:  
          _"I want her."_  
          Not the perfect version.  
          Not the filtered version.  
          Just **you.**  
          <br /><br />

          You are my calm and my chaos.  
          My best fight and my favorite peace.  
          My reason to write, and my home to return to.  
          <br /><br />

          I dont promise a perfect love story  but I promise effort, presence, patience, and the kind of love that stays.  
          Always yours,<br />
           The guy who still melts at your Ayu
        </p>
      </div>
    </div>
  );
}
